<template>

    <div class="markdown">
        <h2>Markdown子組件</h2>

        <div class="dashboard-wrap">
            <div class="editor-wrap">
                <h3>單行標題轉換</h3>
                <input v-model="data.markdown" />
            </div>
            <div class="view-wrap" v-html="data.html"></div>
        </div>
    </div>

</template>

<script>
import { reactive, computed } from 'vue'

export default {
    name: 'MarkdownEditor',
    setup() {
        const data = reactive({
            markdown: '#測試文字',
        })

        data.html = computed(()=>{
            let h1 = data.markdown.match(/^#(.*)/);
            let h2 = data.markdown.match(/^##(.*)/);
            let h3 = data.markdown.match(/^###(.*)/);
            let h4 = data.markdown.match(/^####(.*)/);
            let h5 = data.markdown.match(/^#####(.*)/);
            let h6 = data.markdown.match(/^######(.*)/);

            let result = null;

            if ( h1 ) result = `<h1>${h1[1]}</h1>`;
            if ( h2 ) result = `<h2>${h2[1]}</h2>`;
            if ( h3 ) result = `<h3>${h3[1]}</h3>`;
            if ( h4 ) result = `<h4>${h4[1]}</h4>`;
            if ( h5 ) result = `<h5>${h5[1]}</h5>`;
            if ( h6 ) result = `<h6>${h6[1]}</h6>`;

            if ( !result ) result = data.markdown;

            return result
        })

        return {
            data
        }
    }
}
</script>

<style lang="scss">
.markdown {
    background-color: lightblue;
    padding: 10px;

    .dashboard-wrap {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        column-gap: 10px;

        .editor-wrap {
            
            input {
                padding: 10px;
                width: 100%;
                background-color: #222;
                color: #ddd;
            }
        }

        .view-wrap {
            background-color: white;
        }
    }
}
</style>